.header {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100vh;
  padding: 20px;
  height: 30px;
  z-index: 1.5;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  transition-timing-function: ease-in;
  transition: all 0.5s;
  list-style: none;
}
