.shows {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.show-email {
  background-color: transparent;
  color: #fff !important;
  border: none !important;
  width: 250px;
  padding: 10px;
  margin-bottom: 10px;
}
.show-email:hover {
  border: none !important;
  outline: none;

  /* background-color: rgb(226, 224, 224); */
}
.show-email:focus {
  border: none !important;
  outline: none;
  text-align: center;
  /* background-color: #fff; */
}
#select {
  margin-right: 10px;
  margin-bottom: 10px;
}
.show-btn {
  border: none;
  background-color: transparent;
  color: #fff;
}
.show-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
