body {
  /* background-color: black; */
  font-family: Courier, Courier New, Lucida Sans Typewriter, Lucida Typewriter,
    monospace;
  height: 100vh;
  font-size: 16px;
}
.background {
  /* background: linear-gradient(0deg, rgba(24, 24, 24, 0.3), rgba(9, 9, 9, 0.3)),
    url('./img/illyminiachi.jpg');
  background-position: initial;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover; */
  background-color: black;
  color: #fff;
}
.app {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 20px;
}
.app::-webkit-scrollbar {
  display: none;
}
.music-image {
  max-height: 400px;
}
.image {
  height: 400px;
  width: 100%;
  object-fit: cover;
  object-position: center;
}
.mylink {
  display: flex !important;
  justify-content: space-between !important;
}

@media only screen and (max-width: 600px) {
  .image {
    height: 150px;
    width: 150px;
    object-fit: cover;
    object-position: center;
  }
}
.shop-footer {
  width: 80vw;
  /* height: 100vh; */
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  font-size: 12px;
  /* position: fixed;
  bottom: 3; */
}
.shop-link:hover {
  border: 1px solid black;
}
